/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect } from 'react';
import { injectIntl } from 'react-intl';
import {
  Row,
  Col,
  Button,
  Upload,
  Icon,
  Form,
  message,
  Select,
  Alert,
  Radio,
  Input,
  Divider,
  Typography,
  Spin,
} from 'antd';

import parse from 'html-react-parser';

import { ButtonWrapper, ColWrapper, FormItemWrapper, Div, DivError, DivButtons } from './../Styled';

import SuccessCmp from './../SuccessCmp';
import ErrorCmp from './../ErrorCmp';
import ProcessList from './../ProcessList';

import functions from './../../utils/functions';

import { ModalWrapper } from './styles';

import {
  processFile,
  quoteFile,
  translate,
  enginesByUserv2,
  getProcessTypes,
} from './../../services';

import './index.css';

const Dragger = Upload.Dragger;
const Option = Select.Option;
const { TextArea } = Input;
const { Paragraph } = Typography;

require('./file-download');

const ProcessServicesCmp = props => {
  const {
    intl: { messages },
    userLogin,
    newProcess,
    defaultValue,
    setCollapsed,
  } = props;

  const [visible, setVisible] = useState(false);
  const [loading, setLoading] = useState(true);

  const [engines, setEngines] = useState([]);
  // const [process, setProcess] = useState([]);
  const [selected, setSelected] = useState({ name: null, engines: [] });

  const [radioSelect, setRadioSelect] = useState(1); // 0 Upload File 1 Translate
  const [view, setView] = useState('list');
  const [viewNoty, setViewNoty] = useState('process');
  const [fileList, setFileList] = useState([]);
  const [uploading, setUploading] = useState(false);
  const [translating, setTranslating] = useState(false);
  const [result, setResult] = useState('Translation');
  const [processEnd, setProcessEnd] = useState(false);
  const [showError, setShowError] = useState(false);
  const [source, setSource] = useState(null);
  const [target, setTarget] = useState(null);
  const [defaultSourceValue, setDefaultSourceValue] = useState(null);
  const [defaultTargetValue, setDefaultTargetValue] = useState(null);

  const [fetching, setFetching] = useState(false);
  const [process, setProcess] = useState([]);
  const [processUser, setProcessUser] = useState(null);
  const [engine, setEngine] = useState(null);
  const [defaultEngineValue, setDefaultEngineValue] = useState(null);

  useEffect(() => {
    fetchEngines();
    fetch();
    return function cleanup() {
      setCollapsed(false);
    };
  }, [defaultValue, newProcess]);

  const initStateView = view => {
    setView(view);
    setViewNoty('process');
    setFileList([]);
    setUploading(false);
    setProcessEnd(false);
    setShowError(false);
  };

  const fetchEngines = () => {
    enginesByUserv2()
      .then(response => {
        const pss = response.data.process;

        const { processTypes, subprocessTypes } = pss[0];
        setProcessUser({
          processTypes,
          subprocessTypes,
        });
        if (pss.length) {
          if (!newProcess) {
            changeView('list');
          } else {
            changeView('process');
          }
        }

        initSelected(pss);
        setLoading(false);
      })
      .catch(error => {
        message.error(error.message);
        setLoading(false);
      });
  };

  const fetch = async () => {
    try {
      setFetching(true);
      const { data } = await getProcessTypes();
      const array = [];
      data.forEach(element => {
        if (element.SubprocessTypes && element.SubprocessTypes.length) {
          element.SubprocessTypes.forEach(item => {
            array.push({
              type: 'subprocess',
              item: item,
              parent: element,
            });
          });
        } else {
          array.push({
            type: 'process',
            item: element,
          });
        }
      });

      setProcess(array);
      setFetching(false);
    } catch (error) {
      setFetching(false);
    }
  };

  const changeView = view => {
    if (view === 'process') {
      setCollapsed(true);
    } else {
      setCollapsed(false);
    }
    setView(view);
  };

  const initSelected = _engines => {
    let selected = null;
    let array = [];

    if (_engines && _engines.length) {
      _engines.forEach(element => {
        if (props.defaultValue && element.name === props.defaultValue.toLowerCase()) {
          setSelected(element);
          setEngines(element.engines);
          selected = element;
        }
      });

      if (selected) {
        if (selected.engines && selected.engines.length) {
          selected.engines.forEach(element => {
            let flag = false;
            for (let i = 0; i < array.length; i++) {
              if (array[i].key === element.source) {
                flag = true;
                break;
              }
            }
            if (!flag) {
              let key = element.source;
              array.push({
                key,
                id: element.id,
                name: functions.languages[key],
                target: element.target,
                source: element.source,
              });
            }
          });
          if (array.length) {
            setDefaultSourceValue(array[0].name);
            setSource(array[0].source);
            setTarget(array[0].target);
            setDefaultTargetValue(getTargets(selected.engines, array[0].source)[0].text);
            setDefaultEngineValue(
              getEngines(selected.engines, array[0].source, array[0].target)[0].text
            );
          }
        }
      }
    } else {
      // message.warning("You don't have engines assigned !");

      changeView('list');
    }
  };

  const handleSubmit = e => {
    e.preventDefault();
    const { typeOfUser, changeFileID } = props;

    const _engines = getEngines();

    // eslint-disable-next-line array-callback-return
    let nameEng = _engines.filter(item => {
      if (item && item.key === engine) {
        return item;
      }
    })[0];

    if (_engines && _engines.length && !nameEng) {
      nameEng = _engines[0];
    }

    if (nameEng) {
      setEngine(engine);
    } else {
      alert('PROBLEm');
    }

    setShowError(false);
    setViewNoty('process');

    props.form.validateFields(async (err, values) => {
      if (!err) {
        let aux = nameEng.text.split('-');
        if (radioSelect === 1) {
          if (fileList.length && nameEng) {
            let form = {
              processId: aux[0],
              processName: selected.name,
              processType: values.process,
              engineId: nameEng.id,
              engineName: nameEng.name,
              engineDomain: nameEng.domain,
              engineSource: nameEng.source,
              engineTarget: nameEng.target,
              files: fileList,
            };
            setUploading(true);

            if (typeOfUser === 2 || typeOfUser === 3) {
              quoteFile(form)
                .then(response => {
                  message.success(messages.default.success);
                  setUploading(false);
                  setViewNoty('success');
                  setFileList([]);
                  changeFileID(response.data.fileId);
                })
                .catch(error => {
                  message.error(error.message);
                  setUploading(false);
                  setViewNoty('error');
                });
            } else {
              processFile(form)
                .then(response => {
                  message.success(messages.default.success);
                  setUploading(false);
                  setViewNoty('success');
                  setFileList([]);
                  changeFileID(response.data.fileId);
                })
                .catch(error => {
                  message.error(error.message);
                  setUploading(false);
                  setViewNoty('error');
                });
            }
          } else {
            setShowError(true);
          }
        } else {
          const form = {
            engine: nameEng,
            processType: values.process,
            text: values.text,
          };

          try {
            setTranslating(true);
            const response = await translate({ ...form });
            setResult(response.data.data[0][0].tgt);
            setTranslating(false);
          } catch (error) {
            setTranslating(false);
            message.error(error.message);
          }
        }
      }
    });
  };

  const handleChange = value => {
    let selected = null;
    let array = [];

    engines.forEach(element => {
      if (element.name === value.toLowerCase()) {
        setSelected(element);
        selected = element;
      }
    });

    if (selected) {
      if (selected.engines && selected.engines.length) {
        selected.engines.forEach(element => {
          let flag = false;
          for (let i = 0; i < array.length; i++) {
            if (array[i].key === element.source) {
              flag = true;
              break;
            }
          }
          if (!flag) {
            let key = element.source;
            array.push({
              key,
              id: element.id,
              name: functions.languages[key],
              target: element.target,
              source: element.source,
            });
          }
        });
        if (array.length) {
          setDefaultSourceValue(array[0].name);
          setSource(array[0].source);
          //element["key"] = `${element.id}-${element.target}`;
          setTarget(`${array[0].id}-${array[0].target}`);
          setDefaultTargetValue(getTargets(selected.engines, array[0].source)[0].text);
          setDefaultEngineValue(
            getEngines(selected.engines, array[0].source, array[0].target)[0].text
          );
        }
      }
    }
  };

  const changeSource = source => {
    setSource(source);
    const tname = getTargets(null, source)[0];
    setDefaultTargetValue(tname.text);

    const ename = getEngines(null, source, tname.target)[0].text;
    setDefaultEngineValue(ename);
  };

  const changeTarget = tgt => {
    setTarget(tgt);
    const aux = getTargets().filter(item => item.key === tgt);
    if (aux && aux.length) {
      setDefaultTargetValue(aux[0].text);
      const ename = getEngines(null, source, aux[0].target)[0].text;
      setDefaultEngineValue(ename);
    }
  };

  const changeEngine = value => {
    setEngine(value);
    const aux = getEngines().filter(item => item.key === value);
    if (aux && aux.length) {
      setDefaultEngineValue(aux[0].text);
    }
  };

  const getSources = () => {
    let array = [];
    selected.engines.forEach(element => {
      let flag = false;
      for (let i = 0; i < array.length; i++) {
        if (array[i].key === element.source) {
          flag = true;
          break;
        }
      }
      if (!flag) {
        let key = element.source;
        array.push({ key, name: functions.languages[key] });
      }
    });
    return array;
  };

  const onChange = e => {
    setRadioSelect(e.target.value);
  };

  const getTargets = (_engines = null, _source = null) => {
    if (!_engines) {
      _engines = engines;
    }
    if (!_source) {
      _source = source;
    }

    let array = [];
    _engines.forEach(element => {
      let flag = false;
      for (let i = 0; i < array.length; i++) {
        if (array[i].key === element.target) {
          flag = true;
          break;
        }
      }

      if (_source) {
        if (!flag && element.source === _source) {
          if (element.domain) {
            // element['key'] = `${element.id}-${element.target}`;
            element['key'] = element.target;
            // element['text'] = `${functions.languages[element.target]} - ${element.name} - ${
            //   element.domain
            // }`;
            element['text'] = `${functions.languages[element.target]}`;
          } else {
            // element['key'] = `${element.id}-${element.target}`;
            element['key'] = element.target;
            // element['text'] = `${functions.languages[element.target]} - ${element.name}`;
            element['text'] = `${functions.languages[element.target]}`;
          }
          array.push(element);
        }
      }
    });
    return array;
  };

  const getEngines = (_engines = null, _source = null, _target = null) => {
    if (!_engines) {
      _engines = engines;
    }
    if (!_source) {
      _source = source;
    }
    if (!_target) {
      _target = target;
    }

    let array = [];
    _engines.forEach(element => {
      let flag = false;
      for (let i = 0; i < array.length; i++) {
        if (array[i].key === element.name) {
          flag = true;
          break;
        }
      }

      if (_source && _target) {
        if (!flag && element.source === _source && element.target === _target) {
          if (element.domain) {
            element['key'] = element.name;
            element['text'] = element.name;
          } else {
            element['key'] = element.name;
            element['text'] = element.name;
          }
          array.push(element);
        }
      }
    });
    return array;
  };

  const renderProcess = () => {
    const {
      form: { getFieldDecorator, getFieldsError },
    } = props;

    const dprops = {
      onRemove: file => {
        const index = fileList.indexOf(file);
        const newFileList = fileList.slice();
        newFileList.splice(index, 1);
        setFileList(newFileList);
      },

      beforeUpload: file => {
        const isLt2M = file.size / 1024 / 1024 < 500;
        if (!functions.checkFile(file, props.allowedFiles)) {
          message.error(
            messages.process.error.uploadOne.replace('allowedFiles', props.allowedFiles)
          );
        } else if (!isLt2M) {
          message.error(messages.process.error.uploadTwo);
        }
        return false;
      },
      onChange: info => {
        let aux = [];
        info.fileList.map(item => {
          if (functions.checkFile(item, props.allowedFiles)) {
            aux.push(item);
          }
          return aux;
        });

        setFileList(aux);
      },
      multiple: true,
      fileList,
    };

    const activeEngines = JSON.parse(JSON.stringify(getEngines()));

    const targets = getTargets();

    let flag = false;
    for (let index = 0; index < targets.length; index++) {
      if (targets[index].text === defaultTargetValue) {
        flag = true;
        break;
      }
    }
    if (targets.length && !flag) {
      setDefaultTargetValue(targets[0].text);
    }

    if (viewNoty === 'process') {
      let df = false;
      const options = process.map(d => {
        if (processUser) {
          if (d.parent) {
            const p = processUser.processTypes.filter(el => el.id === d.parent.id);
            const s = processUser.subprocessTypes.filter(el => el.id === d.item.id);
            if (p.length && s.length) {
              if (!df) df = d.item.variableName;
              return (
                <Option key={d.item.id} value={d.item.variableName}>
                  {d.item.name.replace(/^\w/, c => c.toUpperCase())}
                </Option>
              );
            } else {
              return null;
            }
          } else {
            const p = processUser.processTypes.filter(el => el.id === d.item.id);
            if (p.length) {
              if (!df) df = d.item.variableName;
              return (
                <Option key={d.item.id} value={d.item.variableName}>
                  {d.item.name.replace(/^\w/, c => c.toUpperCase())}
                </Option>
              );
            } else {
              return null;
            }
          }
        } else {
          if (!df) df = d.item.variableName;
          return (
            <Option key={d.item.id} value={d.item.variableName}>
              {d.item.name.replace(/^\w/, c => c.toUpperCase())}
            </Option>
          );
        }
      });

      const formItemLayout = {
        labelCol:
          radioSelect === 1
            ? {
                xs: { span: 24 },
                sm: { span: 24 },
              }
            : {
                xs: { span: 24 },
                sm: { span: 10 },
              },
        wrapperCol:
          radioSelect === 1
            ? {
                xs: { span: 24 },
                sm: { span: 24 },
              }
            : {
                xs: { span: 24 },
                sm: { span: 14 },
              },
      };

      return (
        <div>
          <Row>
            <Col>
              <Form onSubmit={handleSubmit}>
                <Row type="flex" justify="center">
                  <ColWrapper xs={24} md={12}>
                    <Radio.Group onChange={onChange} value={radioSelect}>
                      <Radio value={1}>Upload File</Radio>
                      <Radio value={0}>Translate Text</Radio>
                    </Radio.Group>
                  </ColWrapper>
                  <ColWrapper md={12}>
                    <ButtonWrapper
                      type="primary"
                      icon="rollback"
                      onClick={() => {
                        changeView('list');
                      }}
                    >
                      {messages.process.listProcess}
                    </ButtonWrapper>
                  </ColWrapper>
                  <Divider />
                  <ColWrapper xs={24} md={radioSelect === 1 ? 12 : 24}>
                    <Row type="flex" justify="center" align="middle">
                      <Col xs={24}>
                        <Row
                          type="flex"
                          justify="space-between"
                          className={radioSelect === 1 ? '' : 'translate-mode'}
                        >
                          <Col xs={24} md={{ span: radioSelect === 1 ? 24 : 5 }}>
                            <FormItemWrapper
                              flexCenter={radioSelect === 1 ? false : true}
                              {...formItemLayout}
                              mode={radioSelect}
                              label={messages.process.process}
                              hasFeedback
                            >
                              {getFieldDecorator('process', {
                                initialValue: df,
                                disabled: source === null || processEnd,
                                rules: [
                                  {
                                    required: true,
                                    message: messages.process.rules.process.required,
                                  },
                                ],
                              })(<Select autofocus="true">{options}</Select>)}
                            </FormItemWrapper>
                          </Col>
                          <Col
                            xs={24}
                            md={{
                              span: radioSelect === 1 ? 24 : 5,
                            }}
                          >
                            <FormItemWrapper
                              flexCenter={radioSelect === 1 ? false : true}
                              {...formItemLayout}
                              mode={radioSelect}
                              label={messages.process.source}
                              hasFeedback
                            >
                              {getFieldDecorator('source', {
                                initialValue: defaultSourceValue,
                                disabled: processEnd,
                                rules: [
                                  {
                                    required: true,
                                    message: messages.process.rules.source.required,
                                  },
                                ],
                              })(
                                <Select
                                  key={getSources().length}
                                  disabled={!getSources().length}
                                  onChange={changeSource}
                                >
                                  {getSources().map(element => {
                                    return <Option key={element.key}>{element.name}</Option>;
                                  })}
                                </Select>
                              )}
                            </FormItemWrapper>
                          </Col>
                          <Col
                            xs={24}
                            md={{
                              span: radioSelect === 1 ? 24 : 5,
                            }}
                          >
                            <FormItemWrapper
                              flexCenter={radioSelect === 1 ? false : true}
                              {...formItemLayout}
                              mode={radioSelect}
                              label={messages.process.target}
                              hasFeedback
                            >
                              <Select
                                key={targets.length}
                                value={defaultTargetValue}
                                onChange={changeTarget}
                                disabled={source === null || processEnd}
                              >
                                {targets.map(element => {
                                  return <Option key={element.key}>{element.text}</Option>;
                                })}
                              </Select>
                            </FormItemWrapper>
                          </Col>
                          <Col
                            xs={24}
                            md={{
                              span: radioSelect === 1 ? 24 : 5,
                            }}
                          >
                            <FormItemWrapper
                              flexCenter={radioSelect === 1 ? false : true}
                              {...formItemLayout}
                              mode={radioSelect}
                              label="Engines"
                              hasFeedback
                            >
                              <Select
                                key={activeEngines.length}
                                value={defaultEngineValue}
                                onChange={changeEngine}
                                disabled={source === null || processEnd}
                              >
                                {activeEngines.map(element => {
                                  return <Option key={element.key}>{element.text}</Option>;
                                })}
                              </Select>
                            </FormItemWrapper>
                          </Col>
                        </Row>
                      </Col>
                    </Row>
                  </ColWrapper>
                  <ColWrapper xs={24} md={radioSelect === 1 ? 12 : 24}>
                    <Row>
                      <Col xs={24}>
                        {radioSelect === 1 && (
                          <FormItemWrapper>
                            <Div error={showError && !fileList.length}>
                              <Dragger {...dprops}>
                                <p className="ant-upload-drag-icon">
                                  <Icon type="inbox" />
                                </p>
                                <p className="ant-upload-text">{messages.process.drag}</p>
                              </Dragger>

                              {showError && !fileList.length ? (
                                <Alert
                                  style={{ margin: 5 }}
                                  message={messages.process.alert}
                                  type="error"
                                  showIcon
                                />
                              ) : null}
                            </Div>
                          </FormItemWrapper>
                        )}
                        {radioSelect === 0 && (
                          <React.Fragment>
                            <Row>
                              <Col xs={24} md={12}>
                                <FormItemWrapper style={{ marginTop: 30 }} hasFeedback>
                                  {getFieldDecorator('text', {
                                    rules: [
                                      {
                                        required: true,
                                      },
                                    ],
                                  })(<TextArea className="language-textarea" autosize />)}
                                </FormItemWrapper>
                              </Col>
                              <Col xs={24} md={12}>
                                <FormItemWrapper>
                                  <div>
                                    <Paragraph
                                      className="language-textarea"
                                      style={{
                                        backgroundColor: 'rgb(240, 242, 245)',
                                        margin: '30px 0px 0 20px',
                                        minHeight: 80,
                                        padding: '0px 10px 0 10px',
                                      }}
                                    >
                                      {parse(result.replace(/(?:\r\n|\r|\n)/g, '<br />'))}
                                    </Paragraph>
                                  </div>
                                </FormItemWrapper>
                              </Col>
                            </Row>
                          </React.Fragment>
                        )}
                      </Col>
                    </Row>
                  </ColWrapper>
                </Row>
                <Row>
                  <Col xs={24}>
                    {radioSelect === 1 && (
                      <FormItemWrapper
                        style={{
                          display: 'flex',
                          justifyContent: 'center',
                        }}
                      >
                        {!processEnd ? (
                          <Button
                            style={{ marginTop: 20 }}
                            icon="file"
                            type="primary"
                            htmlType="submit"
                            size="large"
                            loading={uploading}
                            disabled={
                              !getSources().length ||
                              !target ||
                              !source ||
                              functions.hasErrors(getFieldsError())
                            }
                          >
                            {uploading
                              ? messages.process.uploading
                              : messages.process.uploadingLoad}
                          </Button>
                        ) : null}
                      </FormItemWrapper>
                    )}
                    {radioSelect === 0 && (
                      <FormItemWrapper
                        style={{
                          display: 'flex',
                          justifyContent: 'center',
                        }}
                      >
                        {!processEnd ? (
                          <Button
                            style={{ marginTop: 20 }}
                            icon="global"
                            type="primary"
                            htmlType="submit"
                            size="large"
                            loading={translating}
                          >
                            {translating
                              ? messages.process.translating
                              : messages.process.translate}
                          </Button>
                        ) : null}
                      </FormItemWrapper>
                    )}
                  </Col>
                </Row>
              </Form>
            </Col>
          </Row>
          <ModalWrapper
            title="Files List"
            onCancel={hideListFile}
            visible={visible}
            footer={null}
            closable
          >
            <div className="ant-upload-list ant-upload-list-text">
              {fileList.map(item => {
                return (
                  <div
                    key={item.uid}
                    className="ant-upload-list-item ant-upload-list-item-undefined"
                  >
                    <div className="ant-upload-list-item-info">
                      <span>
                        <Icon type="paper-clip" />
                        <span className="ant-upload-list-item-name" title={item.name}>
                          {item.name}
                        </span>
                      </span>
                    </div>
                    <Icon
                      onClick={() => {
                        onRemove(item);
                      }}
                      type="close"
                    />
                  </div>
                );
              })}
            </div>
          </ModalWrapper>
        </div>
      );
    }
    if (viewNoty === 'error') {
      return (
        <div>
          <Row>
            <Col>
              <ButtonWrapper
                type="primary"
                icon="rollback"
                onClick={() => {
                  changeView('list');
                }}
              >
                {messages.process.listProcess}
              </ButtonWrapper>
            </Col>
          </Row>
          <Row>
            <Col>
              <DivError>
                <ErrorCmp messages={messages} />
                <DivButtons>
                  <Button
                    onClick={() => {
                      initStateView('process');
                    }}
                    type="primary"
                    size="large"
                  >
                    {messages.process.tryAgain}
                  </Button>
                </DivButtons>
              </DivError>
            </Col>
          </Row>
        </div>
      );
    }
    if (viewNoty === 'success') {
      return (
        <div>
          <Row>
            <Col>
              <ButtonWrapper
                type="primary"
                icon="rollback"
                onClick={() => {
                  initStateView('list');
                }}
              >
                {messages.process.listProcess}
              </ButtonWrapper>
            </Col>
          </Row>
          <Row>
            <Col>
              <DivError>
                <SuccessCmp messages={messages} />
                <DivButtons>
                  <Button
                    onClick={() => {
                      initStateView('process');
                    }}
                    type="primary"
                    size="large"
                  >
                    {messages.process.another}
                  </Button>
                </DivButtons>
              </DivError>
            </Col>
          </Row>
        </div>
      );
    }
  };

  const hideListFile = () => {
    setVisible(false);
  };

  const onRemove = file => {
    if (!processEnd) {
      let aux = [];
      fileList.map(item => {
        if (item.uid !== file.uid) {
          aux.push(item);
        }
        return aux;
      });
      if (!aux.length) hideListFile();
      setFileList(aux);
    }
  };

  return (
    <div>
      {props.children}

      <Spin spinning={loading}>
        <div style={{ padding: 24, background: '#fff', minHeight: 360 }}>
          {view === 'list' ? (
            <ProcessList engines={engines} userLogin={userLogin} changeView={changeView} />
          ) : (
            renderProcess()
            // <CreateProcessForm/>
          )}
        </div>
      </Spin>
    </div>
  );
};

const WrappedProcessServicesCmp = Form.create()(injectIntl(ProcessServicesCmp));

export default WrappedProcessServicesCmp;
